.player-stats {
  margin: 20px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 8px;
  background-color: #f9f9f9;
}

.player-stats h3 {
  margin-bottom: 10px;
  font-size: 1.2rem;
  color: #333;
}

.player-stats ul {
  list-style: none;
  padding: 0;
}

.player-stats li {
  margin: 5px 0;
  font-size: 1rem;
}